<template>
  <div id="app">
      <HeadTop></HeadTop>
      <transition name="animate"><router-view class="content"></router-view></transition>
      <FootEnd></FootEnd>
  </div>
</template>

<script>
    import HeadTop from './components/HeadTop'
    import FootEnd from './components/Foot'
export default {
    components:{HeadTop,FootEnd}
}
</script>

<style>

  .content{
    transition:0.3s;
    position: absolute;
    top:89px;
    bottom:50px;
    width: 100%;
    overflow: auto;
  }
  a{text-decoration: none;}
  img{display: block}
  .animate-enter{transform:translate(100%,0); opacity:0;}
  .animate-leave-active{transform:translate(-100%,0); opacity:0;}
  body,ul,h3,p{margin:0;padding:0;font-family: "Microsoft YaHei UI"}
  li{list-style: none;}
  .mint-msgbox-btns{display:inline;margin-bottom: 38px;}
  .mint-msgbox-content{border: none;padding: 10px 0 0 0;}
  .mint-msgbox-btn{background-color: steelblue;color: #ffffff;font-size:16px;}
  .mint-msgbox-confirm{margin:0 auto;}
  .mint-msgbox{width:302px;height:216px;}
  .mint-msgbox-message{font-size:16px; color: #333333;padding:40px 0 30px 0;}
  .mint-msgbox-title{font-size:20px;}
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
</style>



