<template>
  <div>
    <h3 class="news_title">{{this.$route.params.title}}</h3>
    <span class="news_time">发布时间：{{this.$route.params.time}}</span>
    <p class="news_text">{{this.$route.params.text}} </p>
    <button @click="back">返回</button>
  </div>
</template>

<script>
  export default {
    data(){
      return{

      }
    },
    methods:{
      back(){
        this.$router.go(-1);//返回
      }
    }
  }
</script>

