<template>
  <div class="FormSub">
    <mt-field label="姓名" placeholder="请输入姓名" v-model="username"></mt-field>
    <mt-field label="手机" placeholder="请输入手机" type="tel" v-model="phone"></mt-field>
    <mt-field label="qq" placeholder="请输入qq" type="number" v-model="qq"></mt-field>
    <div class="selCoz">
      <mt-radio
        title="请选择试听课程"
        v-model="course"
        :options="['雅思', '少儿英语', '日语']">
      </mt-radio>
    </div>


    <mt-button @click="sub" type="danger">申请试听</mt-button>
  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        username:'',
        phone:'',
        qq:'',
        course:'',
        arr:[]
      }
    },
    methods:{
      sub(){
        if(this.username && this.phone && this.qq && this.course) {
          this.arr.unshift({
            username: this.username,
            phone: this.phone,
            qq: this.qq,
            course: this.course
          })
          MessageBox.alert('课程申请成功!').then(action => {
            this.$router.push('/home')
          });
        }else{
          MessageBox.alert('请正确输入信息!')
        }



      },

    }
  }
</script>
<style>

</style>



