<template>
    <div class="foot_nav">
        <router-link :to="{path:'/Form'}"><span><img src="../assets/images_home/tab_iocn_62.png">申请试听</span></router-link>
        <span @click="tel"><img src="../assets/images_home/tab_iocn_64.png">联系电话</span>
        <span><img src="../assets/images_home/tab_iocn_66.png">联系我们</span>
    </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        num:'0826-1234567',
      }
    },
    methods:{
      tel(){
        MessageBox({
          title:'',
          message: this.num,
          confirmButtonText:'立 即 拨 号',
        }).then(action => {
          window.location.href = 'tell//'+this.num;
        })
      }
    }
  }
</script>


<style scoped>


</style>



